* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body{
	font-family: 'Lato', sans-serif;
}

h1,
h2,
h3 {
	color: rgb(230, 230, 230);
}

h3, h4{
	font-weight: 400;
	color: rgb(175, 175, 175)
}

.App{
	height: 100vh;
	transition: all 0.5s ease;
}

.library-active{
	margin-left: 15%;
}


@import './song';
@import './player';
@import './library';
@import './nav'